.js-style {
  display: none;
}

.loader {
  width: 48px;
  height: 48px;
  background: #fff;
  border-radius: 50%;
  animation: roll 1s ease-in-out infinite alternate;
  position: relative;
}

.loader:after {
  content: "";
  border: 5px solid #20f;
  border-color: #20f rgba(0, 0, 0, 0);
  border-radius: 50%;
  position: absolute;
  top: 5px;
  bottom: 5px;
  left: 5px;
  right: 5px;
}

@keyframes roll {
  0% {
    transform: translateX(-150%)rotate(0deg);
  }

  100% {
    transform: translateX(150%)rotate(360deg);
  }
}

.card-info {
  color: #010125;
  border: 2px solid #9eedea;
  border-radius: 2px;
  padding-left: 20px;
  padding-right: 20px;
  font-family: Verdana;
  font-size: 12px;
  display: block;
  box-shadow: 2px 2px 4px 1px rgba(124, 62, 62, .227), 2px 1px 1px rgba(0, 0, 0, .14), 2px 2px 1px -1px rgba(0, 0, 0, .12);
}

.cat-info {
  padding-top: 20px;
  display: flex;
}

.photo {
  box-shadow: 10px 10px 10px #88c1e7;
}

